body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

    background-color: orange;
    background-size: cover;
    background-image:url(https://pixabay.com/get/g20154394e4bc599544e1e686dafd779d0f9fa6eac5578f24fad6b405e02e9fe6c10ce89d57c4e171280e1a61f29cb5e0.jpg);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: white;
  height: 100vh;
}

* {
  text-decoration: none;
}

input:focus, textarea:focus, select:focus {
  outline: none;
}

.light-color-bg { 
  background-color: #dcd1ff;
}

.light-color-text { 
  color: #dcd1ff;
}

.dark-color-bg { 
  background-color: #3E2195;
}

.dark-color-text { 
  color: #3E2195;
}

@media screen and (min-width: 560px) {
.App {
  width: 550px;
  margin: auto;
  border-left: solid 1px #3E2195;
  border-right: solid 1px #3E2195;
}

