.header-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-bottom: solid 2px #3E2195;
    height: 4vh;
}

.header-title {
    font-family: 'Fredoka One', bold;
    font-size: 28px;
    -webkit-filter: drop-shadow(2px 2px 1px orange);
            filter: drop-shadow(2px 2px 1px orange);
    vertical-align: middle;
}

.header-links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: right;
    font-family: 'Quicksand', sans-serif;
    vertical-align: middle;
    font-weight: bold;
    font-size: 13px;
}

.header-logo { 
    margin: 0 8px 5px 0;
    height: 30px;
    vertical-align: middle;
}


@media screen and (min-width: 550px) {
.header-wrapper {
    height: 5vh;
}
}

.user-button {
    border-radius: 100%;
    height: 25px;
    width: 25px;
    margin-top: 3px;
}

.user-logged-in {
    background-color: orange;
}

.user-logged-out {
    background-color: rgba(128, 128, 128, 0.603);
}

.cardissue-wrapper {
    border-radius: 20px 20px 20px 0px;
    width: 100px;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.9);
    font-family: 'Quicksand', sans-serif;
}

.cardissue-body {
    height: 50px;
    margin: 0 0 -15px 0;
}
.cardissue-title {
    padding: 5px;
    border-radius: 20px 20px 0px 0px;
    color: white;
    font-size: 15px;
    font-weight: bold;
}

.cardissue-footer a:link, .cardissue-footer a:visited {
    color: white;
}

.cardissue-footer {
    padding: 5px;
    font-size: small;
    border-radius: 0px 0px 20px 0;
}
.votes-wrapper { 
    padding: 5px;
    display: flex;
    justify-content: center;
}

.votes-thumb {
    color: orange;
    -webkit-filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.555));
            filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.555));
    padding: 5px;
}

.votes-thumb:hover,  .votes-thumb:active {
    color: rgb(255, 188, 62);
    -webkit-filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.555));
            filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.555));
}

.votes-number, .votes-number:hover, .votes-number:active {
    font-size: 17px;
    -webkit-filter: drop-shadow(0px 0px 0px rgba(0, 0, 0, 0));
            filter: drop-shadow(0px 0px 0px rgba(0, 0, 0, 0));
}
.issue-wrapper {
    color: white;
    height: 83vh;
}

.issue-title {
    border-radius: 0px 0px 5px 5px;
    padding: 5px;
    margin: 0 0 10px 0;
    text-align: left;
    font-family: 'Quicksand', sans-serif;
}

.issue-section-container {
    margin: 5px;
    text-align: left;
    margin: 0 20px 15px 20px;
}

.issue-center {
    display: flex;
    justify-content: center;
    font-size: small;
    margin: 5px 0 5px 0;
}

.issue-img {
    height: 70%;
    width: auto;
    margin: 10px;
}

.issue-modify-show {
    display: block;
}

.issue-modify-hide {
    display: none;
}

.issue-votes { 
    padding: 10px;
}

.issue-detail {
    display: flex;
    align-items: center;
    margin: 0 0 10px 0;
}

.issue-input { 
    width: 70%;
    border-radius: 5px;
    border: solid 1px rgba(0, 0, 0, 0.308);
    padding: 5px;
    margin: 5px;
    color: rgba(0, 0, 0, 0.685);
}

.issue-input2 {
    width: 65%;
}

.issue-detail2 {
    margin: 10px;
}
.comments-wrapper {
    text-align: left;
    margin: 8px;
    font-size: small;
}
.button {
    border-radius: 15px;
    padding: 5px 10px 5px 10px;
    color: white;
    border: 0;
    font-size: 11px;
    -webkit-filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.25));
    font-family: 'Quicksand', sans-serif;
}

.button:hover, button:active {
    -webkit-filter: drop-shadow(1px 1px 0px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(1px 1px 0px rgba(0, 0, 0, 0.25));
    background-color: orange;
    /* color:#3E2195; */
}
.reportissue-wrapper {
    height: 83vh;
}
.reportissue-section-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: black;
    text-align: left;
    margin: 0 20px 15px 20px;
}

.reportissue-title {
    border-radius: 0px 0px 5px 5px;
    padding: 5px;
    margin: 0 0 10px 0;
    text-align: left;
    color: white;
    font-family: 'Quicksand', sans-serif;
}

.reportissue-detail {
    display: flex;
    justify-content: space-around;
    margin: 5px;
}

.reportissue-center {
    display: flex;
    justify-content: center;
    margin: 10px;
}

.reportissue-checkbox { 
    margin: 5px 0 0 0;
}

.reportissue-small {
    font-size: small;
    width: 100%;
}

.reportissue-input { 
    width: 70%;
    border-radius: 5px;
    border: solid 1px rgba(0, 0, 0, 0.308);
    padding: 5px;
    margin: 0 0 5px 0;
    color: rgba(0, 0, 0, 0.685);
}

.reportissue-left-input {
    justify-content: flex-start;
}

.reportissue-photo { 
    width: 100px;
    height: auto;
}

.hide-photo { 
    display:hide;
}

.reportissue-left {
    text-align: left;
    margin: 20px 0 10px 0;
    width: 73%;
}

hr {
    border: solid 1px #3E2195;
    width: 100%;
}

@media screen and (min-width: 560px) {
