.footer-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    bottom: 0;
    border-top: solid 1px #3E2195;
    height: 9vh;
}

.footer-madeby, .footer-madeby a:link, .footer-madeby a:visited, .footer-madeby a:active {
    font-size: small;
    color: #3E2195;
    padding: 7px 0 0 0;
    font-family: 'Gowun Dodum', sans-serif;
}

.footer-pod, .footer-pod a:link, .footer-pod a:visited, .footer-pod a:active {
    display: flex;
    justify-content: center;
