}

.footer-detail, .footer-detail a:link, .footer-detail a:visited, .footer-detail a:active {
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    margin: auto;
    padding: 5px;
    color: #3E2195;
    font-weight: bold;
    font-family: 'Gowun Dodum', sans-serif;
